import Vue from 'vue'
import Router from 'vue-router'

const Login = resolve => require(['@/views/login.vue'], resolve)
const Index = resolve => require(['@/views/index.vue'], resolve)
const History = resolve => require(['@/views/history.vue'], resolve)


Vue.use(Router)

const router = new Router({
  mode: 'history',
  scrollBehavior (to, from, savedPosition) { // 页面滚动位置，这个功能只在 HTML5 history 模式下可用
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  },
  routes: [
    {
      path: '/',
      component: Index,
	  name: 'view_index'
    },
	{
      path: '/history',
      component: History,
	  name: 'view_history'
    },
    {
      path: '/login',
      component: Login,
	  name: 'view_login'
    }
  ]
})

export default router;



